	module.exports = {"data":{"markdownRemark":{"html":"<p>I'm sorry I have not updated the Web for a while. If you leave it as it is, it is likely to go again like a year, so here's a winter comic announcement. (Since it is an update from the destination, there is no image, but sorry)</p>\n<p>This time, I will participate at December 30 (Friday: <strong>2nd day</strong> ), <strong>Higashi-04a</strong> \"danmaq\". As the main dish this time, <em>we will resell <a href=\"/!/thC/\">Touhou Dreams Drama</a></em> . If you notice it is exactly the 5th anniversary of the release of the dream drama, so I made something like a deluxe pack. All of the following items are packed on one disk.</p>\n<ul>\n<li>Toho Yumimaki Drama .NET Edition (There are not any special features added specifically from the bullet-edged version at the moment, only the stage-by-stage loading will be shortened sharply)</li>\n<li>Toho Dream Terminal Danmaku style version v 2.01 (Barrage style version was also bundled to past replay appreciation <pre-architectural>)</li>\n<li>Oriental time sealed castle barrage style version v1.00</li>\n<li>Bonus: Soundtrack v2 (I intended to put out in the summer 2006 but it is going to be stored in the garbage) is included in the wav file As this time I also made a bunch of copies, so I made a major circle I would be pleased if you could come around slowly and come. Regarding shop consignment of Toho Yume Drama remake version, I think that it will be a little late as I think about it from now on.</li>\n</ul>\n<h2>Outcome Summary</h2>\n<ul>\n<li>\n<p><em>(New publication / game) Dream star Dramax pack \\ 1,000</em> (※ upgrade available)</p>\n</li>\n<li>\n<p>(New issue? /?) Extra \\ 0</p>\n</li>\n<li>\n<p>(Previously published / music CD) <a href=\"/!/leila/\">Leila Prismriver</a> \\ 300</p>\n</li>\n<li>\n<p>(Previously published / music CD) <a href=\"/!/dst/\">danmaq soundtrack</a> \\ 100</p>\n<h2>※ About Upgrade Discount</h2>\n</li>\n</ul>\n<p><img src=\"https://danmaq.com/!/thC/cap_s.jpg\"> <em>For those who came to C81, we</em> will <em>distribute it at a special price in exchange for the items below</em> .</p>\n<p>Only one piece is effective for a part. Also, we will collect replacement items.</p>\n<ul>\n<li>\n<p>Obi castle (press version only) Obi: \\ 1,000 → \\ 800</p>\n</li>\n<li>\n<p>Obi of dreams final v1.00 - 2.01: \\ 1,000 → \\ 200</p>\n</li>\n<li>\n<p>Others have lost Obi! or I bought a jacket instead! : I'm sorry, &#x3C;</p>\n<h2>Note :: About the System Requirements of Dream Drama .NET</h2>\n</li>\n</ul>\n<p><em>Please note that the system requirements differ for .NET versions compared to barrage style versions.</em></p>\n<ul>\n<li>\n<p>Windows XP SP 3 or later is mandatory. For Windows 2000 and XP SP 2 and earlier, only the barrage style version works.</p>\n</li>\n<li>\n<p><strong>Internet always-on connection is mandatory</strong> . Mobile people are aware of pace death!</p>\n</li>\n<li>\n<p>3 GB or more of HDD free space is required (8 GB or more is recommended).</p>\n</li>\n<li>\n<p>When the grabo becomes older than a certain amount, it will suddenly stop working with a certain spec. (GeForce 4Ti, RADEON 8500, i 915 area around the border line)</p>\n</li>\n<li>\n<p>seeing is believing! Those people think that it will be okay if the <a href=\"/!/thC/nph-thC3.0TrGetNightlyBuild.cgi\">trial version</a> works. (Please uninstall before installing because it can not live with product version)</p>\n<h2>About support of dream drama. NET</h2>\n</li>\n</ul>\n<p><a href=\"/!/thC/support/\">We</a> will set up a <a href=\"/!/thC/support/\">dream final drama support page</a> , so please be there. (Because it is under construction now, it is super simple version)</p>","frontmatter":{"cover":null,"date":"2011-12-19T16:42:27+09:00","strDate":"2011/12/19","title":"Winter team (Comic Market 81)"}}},"pathContext":{"path":"/en/blog/2011/12/19-冬の陣コミックマーケット81/","langKey":"en"}}

